.zonghe{
  width: 411px;
  height: 293px;
  .comprehensive{
    width: 137px;
    height: 37px;
    text-align: center;
    line-height: 37px;
    a{
     
      font-size: 16px;
      letter-spacing: 1px;
      color: #ffffff;
      text-decoration: none;
      text-shadow: 0 0 30px rgb(244, 250, 245);
    }
  }
  .realtime{
    margin-top: 10px;
    width: 411px;
    height: 246px;
    .semicircleframe{
      position: relative;
     width: 78px;
     height: 96px;
     float: left;
      .one{
        position: absolute;
        top: 23px;
        left: 28px;
        width: 140%;
        font-size: 12px;
        letter-spacing: 1px;
        color: #fff;
        span{
          font-size: 20px;
          color: #00ffff;
          font-weight: 700;
        }
      }
      .fuwu{
        position: absolute;
        top: 50px;
          left: 28px;
          width: 140%;
        font-size: 14px;
        letter-spacing: 0px;
        color: #ffffff;
      }
    }
  }
}

.semicircleframe:nth-child(1){
  margin: 13px 0 0 16px;
}
.semicircleframe:nth-child(2){
  margin: 13px 0 0 60px;
}
.semicircleframe:nth-child(3){
  margin: 13px 0 0 60px;
}
.semicircleframe:nth-child(4){
  margin: 13px 0 0 16px;
}
.semicircleframe:nth-child(5){
  margin: 13px 0 0 60px;
}
.semicircleframe:nth-child(6){
  margin: 13px 0 0 60px;
}